code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
a {
  text-decoration: none;
}

ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

ul.nav li {
  float: right;
  margin-left: 2%;
  margin-right: 2%;
  height: 100%;
  line-height: 45px;
}

li {
  list-style: none;
}

.svg-wrapper {
  height: 30px;
  margin: 1 auto;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  width: 140px;
}

.shape {
  fill: transparent;
  stroke-dasharray: 90 210;
  stroke-dashoffset: -210;
  stroke-width: 4px;
  stroke: #19f6e8;
}

.text {
  color: #fff;
  font-family: 'Roboto Condensed';
  text-align: center;
  font-size: 12px;
  letter-spacing: 8px;
  line-height: 20px;
  position: relative;
  top: -50px;
}

@keyframes draw {
  0% {
    stroke-dasharray: 140 540;
    stroke-dashoffset: -474;
    stroke-width: 8px;
  }
  100% {
    stroke-dasharray: 760;
    stroke-dashoffset: 0;
    stroke-width: 4px;
  }
}

.svg-wrapper:hover .shape {
  -webkit-animation: 0.5s draw linear forwards;
  animation: 0.5s draw linear forwards;
}

.main-nav {
  width: 100vh;
  height: 65px;
  position: fixed;
  background: #4c4c4c;
  -webkit-transform-origin: left top;
  -webkit-transform: rotate(-90deg) translateX(-100%);
  z-index: 1;
}

.txt-home {
  -webkit-transform-origin: left top;
  -webkit-transform: rotate(90deg) translateX(-10%);
}

.icon {
  color: whitesmoke;
  padding: 2px;
  padding-top: 5px;
  font-size: 2rem;
  border: 1px solid #4c4c4c;
  transition: 1s;
  border-radius: 5px;
}

.icon:hover {
  padding: 2px;
  padding-top: 5px;
  color: #19f6e8;
  font-size: 2rem;
  border: 1px solid #19f6e8;
  border-radius: 5px;
}

html,
body {
  margin: 0;
}

.EyeApp {
  position: relative;
  width: 100vw;
  min-width: 800px;
  height: 100vh;
  min-height: 600px;
  background: linear-gradient(-45deg, #8691b3, #edeef3);
}
.EyeApp *,
.EyeApp *:before,
.EyeApp *:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
}

.cursor {
  width: var(--size);
  height: var(--size);
  border-radius: 50%;
  background: white;
  position: absolute;
  z-index: 999;
  pointer-events: none;
  mix-blend-mode: difference;

  &-f {
    width: var(--size);
    height: var(--size);
    position: absolute;
    top: 0;
    left: 0;
    background-image: url("data:image/svg+xml,%3Csvg width='47' height='47' viewBox='0 0 47 47' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M42.4202 42.4202C38.8403 46 33.3594 46 23.5 46C13.6406 46 8.15966 46 4.57983 42.4202C1 38.8403 1 33.3594 1 23.5C1 13.6406 1 8.15966 4.57983 4.57983C8.15966 1 13.6406 1 23.5 1C33.3594 1 38.8403 1 42.4202 4.57983C46 8.15966 46 13.6406 46 23.5C46 33.3594 46 38.8403 42.4202 42.4202Z' stroke='white'/%3E%3C/svg%3E%0A");
    background-size: cover;
    mix-blend-mode: difference;
    pointer-events: none;
    opacity: 0.5;
  }
}
